<template>
    <yd-layout>
        <div class="mainContentFluid partOne">
            <div class="confirTop">
                <div class="advice">
                    <div class="confirmTrip">
                        请确认是否按计划出差：
                    </div>
                    <div class="selectAdvice">
                        <span id="js_yesBtn" class="selectBtn rSpace" v-bind:class="{selectActive:statusRadio==1}" v-on:click="statusRadio=1">是</span>
                        <span id="js_noBtn" class="selectBtn" v-bind:class="{selectActive:statusRadio==2}" v-on:click="statusRadio=2">否</span>
                    </div>
                </div>
                <div class="adviceTxt confirmRemark">
                    <div class="commonAdvice">备注：</div>
                    <trsTextarea v-model="remark"></trsTextarea>
                </div>
            </div>
        </div>
        <div class="mainContent">
            <div class="contentSradius holiday">
                <div class="holiday_two">
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="hint">
                                    {{formData.applicantname}}出差申请
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.firstorgname!=''">
                            <yd-flexbox>
                                <div class="leftWord unit">
                                    单&nbsp&nbsp位：
                                </div>
                                <div class="rightWord unitColor" v-text="formData.firstorgname"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.travelplace!=''">
                            <yd-flexbox>
                                <div class="leftWord">
                                    出差地点：
                                </div>
                                <div class="rightWord" v-text="formData.travelplace"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="startdate!=''">
                            <yd-flexbox>
                                <div class="leftWord">
                                    开始时间：
                                </div>
                                <div class="rightWord" v-text="startdate"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="enddate!=''">
                            <yd-flexbox>
                                <div class="leftWord">
                                    结束时间：
                                </div>
                                <div class="rightWord" v-text="enddate"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.traveldays!=''">
                            <yd-flexbox>
                                <div class="leftWord">
                                    出差天数：
                                </div>
                                <div class="rightWord">{{formData.traveldays}}天</div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="formData.othertravelnames!=''">
                            <yd-flexbox>
                                <div class="leftWord tripTime">
                                    随行人员：
                                </div>
                                <div class="tripPerson persons" v-text="formData.othertravelnames"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="reason!=''">
                            <div class="tripRemark">出差事由：</div>
                            <div class="tripReason" v-html="reason">
                            </div>
                        </yd-flexbox-item>
                        <yd-flexbox-item v-if="reason2!=''">
                            <div class="tripRemark">行程安排：</div>
                            <div class="tripReason" v-html="reason2">
                            </div>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <div class="mainContentFluid adviceBottom" v-if="aproveList.length!=0">
            <div class="fluidWidth leaderAdvice">
                <yd-flexbox direction="vertical">
                    <yd-flexbox-item>
                        <yd-flexbox>
                            <div class="adviceTitle">领导审批意见：</div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                    <yd-flexbox-item>
                        <yd-flexbox>
                            <div style="width:100%">
                                <div class="label_box" v-for="item in aproveList">
                                    <div class="range_txt attachment" v-if="item.userName1!=''">{{item.userName1}}：</div>
                                    <div class="range_txt attachment" v-text='item.neirong'></div>
                                    <div class="range_txt issueName">
                                        <div class="range issueTime" v-text='getDay(item.submitDate)'></div>
                                    </div>
                                </div>
                            </div>
                        </yd-flexbox>
                    </yd-flexbox-item>
                </yd-flexbox>
            </div>
        </div>
        <yd-tabbar slot="tabbar" v-if="status == 0" bgcolor="#fff">
            <yd-button id="js_confirmBtn" v-on:click.native="confirmBtn()" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe">提交</yd-button>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'confirmTrip',
    data() {
        return {
            remark: '',
            reason: '',
            reason2: '',
            radio2: '1',
            isplan: '',
            formData: [],
            startdate: '',
            enddate: '',
            aproveList: [],
            confirmText: '',
            popupArr: [],
            statusRadio: 1,
            status: this.$route.params.status,
            errorTime: 0
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('出差审批');
        vm.initForm();
        if (vm.status == 0) {
            vm.confirmText = '等待您确认'
        } else if (vm.status == 1) {
            vm.confirmText = '您已完成确认'
        }
        vm.$dialog.loading.open('加载中...');
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        // 表单数据
        initForm() {
            var vm = this;
            vm.getM(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                'type': 'travelremark',
                'workItemID': vm.$route.params.workItemID,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function(res) {
                if (res.isSuccess == 'false') {
                    vm.errorTime++;
                    if (vm.errorTime < 3) {
                        vm.initForm();
                    } else {
                        vm.$dialog.loading.close();
                        vm.$dialog.toast({
                            mes: '表单未加载成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    }
                } else {
                    vm.$dialog.loading.close();
                }
                if (res.jsonData.CURRENTSTATE != 10 && vm.$route.params.status == 0 && res.isSuccess == 'true') {
                    vm.$dialog.toast({
                        mes: '该流程已被处理',
                        timeout: 2000,
                        callback: () => {
                            setTimeout(function() {
                                if (vm.$route.params.status == 0) {
                                    vm.$router.push('/handle');
                                }
                            })
                        }
                    });
                    return false;
                }
                let data = res.jsonData;
                // 审批流程意见
                vm.aproveList = [...res.jsonData.applyopinions, ...res.jsonData.leaderopinions];
                vm.aproveList.sort(function(a, b) {
                    let timeA = a.submitDate.replace(/-/g, '/');
                    let timeB = b.submitDate.replace(/-/g, '/');
                    return Date.parse(timeB) - Date.parse(timeA);
                });
                vm.formData = data;
                vm.startdate = vm.formatDate(data.startdate, 'yyyy-MM-dd');
                vm.enddate = vm.formatDate(data.enddate, 'yyyy-MM-dd');
                vm.reason = data.reason;
                vm.reason2 = data.reason2;
                vm.isplan = data.isplan;
                if (vm.isplan == '1') {
                    vm.statusRadio = 1;
                } else if (vm.isplan == '0') {
                    vm.statusRadio = 2;
                }
                vm.remark = data.remarks;
            }, function(res) {
                if (res.response.status != 200) {
                    vm.$dialog.loading.close();
                    vm.$dialog.toast({
                        mes: '表单未加载成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                }
            })
        },
        // 审批流程
        approveLog() {
            let vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryApprove', {
                'type': 'travelremark',
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'workItemID': vm.$route.params.workItemID
            }, function(res) {
                vm.aproveList = res.jsonData;
            }, function(res) {}, false)
        },
        // 确定按钮
        confirmBtn() {
            let vm = this;
            if (vm.statusRadio == 1) {
                vm.radio2 = '1';
            } else if (vm.statusRadio == 2) {
                vm.radio2 = '0';
            }
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                'type': 'travelremark',
                'workItemID': vm.$route.params.workItemID,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'activityID': 'finishActivity1',
                'leaderIDs': '',
                'fuyan': '',
                'opinion': '',
                'workTitle': vm.formData.firstorgname + vm.formData.applicantname + '出差备案申请',
                'ext': '{"isplan":"' + vm.radio2 + '","remarks": "' + vm.remark + '"}'
            }, function(res) {
                vm.$dialog.toast({
                    mes: '完成确认',
                    timeout: 2000,
                    callback: () => {
                        vm.$router.push('/handle/');
                    }
                });
            });
        },
        getDay(date) {
            date = date.replace(/-/g, '/');
            return (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '日';
        }
    }
}
</script>
<style scoped>
.partOne {
    margin-top: 0;
}

.confirTop {
    padding-top: 0.3rem;
}

.holiday_two {
    border-bottom: none;
    margin: 0;
    padding-bottom: 0;
}

.holiday_two .yd-flexbox-item .yd-flexbox {
    position: relative;
}

.remarkDetail {
    position: absolute;
    top: 0;
    left: 2.3rem;
}

.checkBox {
    height: 1.5rem;
    margin-top: 0.3rem;
    border-top: 0.01rem solid #eaeaea;
    padding: 0 0.3rem;
}

.checkTitle {
    font-size: 0.26rem;
    line-height: 0.8rem;
}

