    padding-left: 0.6rem;
}

.footer {
    /*padding: 0 0.4rem;*/
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1rem;
    z-index: 10;
    background-color: #eaeaea;
}

