    float: right;
    min-height: 2rem;
    width: 5.8rem;
    /*margin-top: 0.1rem;*/
    font-size: 0.32rem;
    padding-right: 0.2rem;
    padding-top: 0.2rem;
    color: #bcbcbc;
}

