    float: right;
    width: 4.5rem;
    margin-left: 1.8rem;
    min-height: 0.5rem;
    word-break: break-all;
    outline: none;
    user-select: text;
    white-space: pre-wrap;
    text-align: left;
}

.tripReason {
    font-size: 0.32rem;
    color: #999;
    min-height: 1rem;
    word-break: break-all;
    outline: none;
    user-select: text;
    white-space: pre-wrap;
    text-align: left;
    line-height: 0.5rem;
    padding-bottom: 0.2rem;
}

.holiday_one {
    padding-top: 0;
    height: 1rem;
    border-bottom: 1px dashed #eaeaea;
    line-height: 1rem;
}

.leaderAdvice {
    padding-right: 0;
