    height: 0.8rem;
    border-bottom: none;
    /*padding-bottom: 0.3rem;*/
    border-bottom: 1px dashed #eaeaea;
}

.confirmTrip {
    font-size: 0.32rem;
    float: left;
    padding-left: 0.3rem;
    color: #333;
}

.secondLine {
    margin-left: 3.2rem;
    width: 1rem;
    text-align: right;
    font-weight: normal;
    margin-top: 0.37rem;
}

.leaderAdvice .yd-flexbox-item {
    border-bottom: none;
    padding-right: 0.1rem;
}

.leaderAdvice .time {
    position: absolute;
    top: -0.2rem;
    right: 0.2rem;
}

.advice .selectAdvice {
    position: relative;
    /*color: #bcbcbc;*/
    padding-right: 0.26rem;
}

.yd-tabbar:after {
    border-top: 0;
}

.adviceTitle {
    font-size: 0.32rem;
    color: #999;
    padding-left: 0.13rem;
}

.firstLine {
    font-weight: normal;
}

.yd-next-icon:before {
    position: absolute;
    top: -0.41rem;
    right: 0.2rem;
}

.attachment {
    color: #333;
    max-width: 7.5rem;
    float: left;
    margin: 0;
    width: auto;
    margin-bottom: .1rem;
}

