    margin-left: 4rem;
    margin-top: 0;
    color: #0a0a0a;
    margin-bottom: .1rem;
}

.fl {
    float: left;
}

.trsTextarea {
    font-size: 0.32rem;
    /*margin-left: 1.2rem;*/
    width: 5.8rem;
    padding-top: 0.2rem;
    padding-bottom: 0.2rem;
    color: #999;
    min-height: 2.14rem;
}

