    color: #333 !important;
}

.rightWord {
    color: #999 !important;
}

.holiday_two .yd-flexbox-item {
    padding: 0;
    min-height: 1rem;
    line-height: 1rem;
    border-bottom: 1px dashed #eaeaea;
